*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


/* Start---> introduction--ShowCase Css */

.intro
{
  position: relative;
  right: 0;
  height: 100vh;
  width: 100%;
  padding: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #111;
  color: #fff;
  z-index: 2;
}

.intro video
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  opacity: 0.8;
}

.heading
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 100px;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.overlay
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #45606d;
  mix-blend-mode: overlay;

}

.intro__text
{
  position: relative;
  z-index: 10;
}

.intro__text h2{
  font-size: 4em;
  font-weight: 800;
  line-height: 1em;
  text-transform: uppercase;
}

.intro__text h3{
  font-size: 2.4em;
  font-weight: 700;
  line-height: 1em;
  /* text-transform: uppercase; */
}
.intro__text p{
  font-size: 1.3em;
  margin: 20px 0;
  font-weight: 800;
  max-width: 900px;
}

.down
{
  position: absolute;
  left: 50%;
  bottom: 2em;
}

span:before{
  content: '';
  animation: animate infinite 4s;
}

@keyframes animate{
  0%{
    content: 'Travel';
  }
  50%
  {
    content: 'Explore';
  }
  100%
  {
    content: 'Enjoy';
  }
}

/* End introduction--ShowCase Css <--- */
.picks{
  height: fit-content;
  width: 100%;
}

.picks__nav
{
 
  padding: 15px;
  display: flex;
  justify-content: space-between;
}
.picks__heading{
  align-content: center;
  align-items: center;
  align-self: center;
}
.picks__filter
{
  display: flex;
  
  justify-content: space-between;
  align-items: center;
}
.filtered_item
{
  display: flex;
  align-items: center;
  
  margin-bottom: 1.5rem;
}
.filtered_item img
{
  width: 13rem;
  height: 13rem;
  margin-right: 2rem;
  margin-left: 2rem;
}
#filtered_img
{
  object-fit: cover;
}

.filtered_text
{
  padding-right: 3rem;
}

/* Destinationssssssssss */

.show__places
{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.dest__main
{
  
  min-height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color:whitesmoke;
}
#dest__img
{
  width: 15rem;
  height: 15rem;
  object-fit: cover;
  margin-right: 1rem;
  margin-top: 1rem;
}

/* visitssssssss */

.visit__page
{
  display: flex;
  justify-content: space-around;
}

.visit__texts
{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 2rem;
  margin: 2rem;
}















/*contackssss*/
*
{
  border-style: none;
}
*:focus {
    outline: 0;
}

.screen
{
  /* background: linear-gradient(to bottom, #33ccff 0%, #ff99cc 100%); */
  background-image: url("./Assets/background.jpg");
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  
}

.input_
{
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28rem;
  height: 32rem;
  flex-direction: column;
  background-color: rgba(54, 52, 52, 0.1);
  backdrop-filter: blur(2px);
  border-radius: 10px;
  margin-left: 2rem;
}
.output_
{
   padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50rem;
  height: fit-content;
  flex-direction: column;
  background-color: rgba(54, 52, 52, 0.2);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  font-size: large;
  font-family:  Helvetica, sans-serif;
  color: #fff;
}
.output_ span
{
  color: black;
}

#name
{
  margin: 1rem;
  font-size: 1rem;
  padding: 1rem;
  width: 18rem;
  height: 3rem;
  border-radius: 8px;
}
.input__name
{
font-size: 2rem;
display: flex;
justify-content: center;
align-items: center;
}

.contact__texts
{
  margin-left: 3rem;
}
#btn
{
   width: 7.5rem;
   border-radius: 5px;
    height: 2rem;
    margin: 1.5rem;
    padding: 0.2rem;
    display: table-cell;
    vertical-align: middle;
    border-style: none;
    background-color:#fff;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1rem;
}
#btn:hover
{
 border-left: 2px solid black;
 border-bottom: 2px solid black;
}

/* footer */

.footer
{
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #111;
  color: #fff;
}
